.float-menu {
    position: fixed;
    top: 90px;
    left: 0;
    width: 100px;
    float: right;
    z-index: 21;
    margin-left: 5px;
}

.float-menu ul {
    list-style: none;


}
.float-menu ul li {
    display: block;
    margin-bottom: 5px;
}
.float-menu ul li a {
    display: block;
    padding: 15px;
    font-weight: bold;
    border: 5px solid white;
    background: #395385;
    color: white;
    text-decoration: none;
    -webkit-box-shadow: 9px 6px 44px -4px rgba(0,0,0,0.75);
    -moz-box-shadow: 9px 6px 44px -4px rgba(0,0,0,0.75);
    box-shadow: 9px 6px 44px -4px rgba(0,0,0,0.75);
}

.float-menu ul li a:hover {
    background: rgba(72, 105, 168, 0.86);
}